.image_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
}

.image {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  object-fit: contain;
  border-radius: 35px;
}

.link {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: center;
}

.title {
  height: 40px;
  font-size: 18px;
  text-align: center;
}
